import { Component, OnInit, OnDestroy } from "@angular/core";
import { WebView } from "@ionic-native/ionic-webview/ngx";
import { LoadingController, Platform } from "@ionic/angular";
import getBlobDuration from "get-blob-duration";

import { gsap } from "gsap";
import { ContentService } from "src/app/services/content.service";
@Component({
  selector: "app-lyric",
  templateUrl: "./lyric.page.html",
  styleUrls: ["./lyric.page.scss"],
})
export class LyricPage implements OnInit, OnDestroy {
  public page;
  public control;
  public videoModal;
  public videoModalVideo;
  public playlistModal;
  public visibleControl = false;
  public isPlaying = false;
  public controlTimeout;
  public lastOnStart = 0;
  public DOUBLE_CLICK_THRESHOLD = 300;
  public preventSingleClick = false;
  public timer;
  public volume = 0;
  public seeker = 0;
  public isFullScreen = false;
  public loading;
  public openedContent;
  public openedSong;
  public contentPath;
  public songs;
  public lyricCount;
  public currentLyric = 0;
  public currentLyricData;
  public seekerTimer;
  public textCurrentDuration = "00:00";
  public trueCurrentDuration = 0;
  public currentDuration = 0;
  public progress = "00:00";
  public mintl1;
  public mintl2;
  public videoModalSrc = "";
  public lyrics;
  public animation;
  public backsong;
  public vocal;
  public backsongEl;
  public vocalEl;
  public mutedVocal = false;
  public lyricHidden = false;
  public loaded = 0;
  constructor(
    public platform: Platform,
    private loadingController: LoadingController,
    public contentService: ContentService,
    private webview: WebView // private fileOpener: FileOpener
  ) {
    this.openedContent = this.contentService.openedContent;
    this.openedSong = this.contentService.openedSong;
    this.contentPath = this.contentService.path;
    if (this.contentPath !== "" && !this.platform.is("electron")) {
      this.contentPath = this.webview.convertFileSrc(
        this.contentService.path + this.openedContent.dirname + "/"
      );
    }
    if (this.contentPath !== "" && this.platform.is("electron")) {
      this.contentPath =
        this.contentService.path + this.openedContent.dirname + "/";
    }
  }

  ngOnInit() {
    this.init();
  }
  async init() {
    this.mintl1 = gsap.timeline();
    this.mintl2 = gsap.timeline();
    this.mintl1.from("#paused", 0.3, { opacity: 0, scale: 0.8 });
    this.mintl1.to("#paused", 0.3, { opacity: 0, scale: 1.2 });
    this.mintl2.from("#played", 0.3, { opacity: 0, scale: 0.8 });
    this.mintl2.to("#played", 0.3, { opacity: 0, scale: 1.2 });
    this.registerAll();
    this.presentLoading();
    let songData = await this.contentService
      .getJson(`songlist/${this.openedSong.url}/song.json`, this.contentPath)
      .toPromise();
    if (songData) {
      this.songs = songData;
    }
    this.control = document.getElementById("control");
    this.videoModal = document.getElementById("videomodal");
    this.videoModalVideo = document.getElementById("videomodalvideo");
    this.playlistModal = document.getElementById("playlistmodal");
    this.page = document.getElementById("page");
    this.control.style.display = "none";
    this.videoModal.style.display = "none";
    this.loadLyric();
  }
  async presentLoading() {
    document.getElementById("page").style.display = "none";
    document.getElementById("loading").style.display = "flex";
  }
  resolveurl(url) {
    return url.includes("https://www.dropbox.com")
      ? url
      : this.contentPath +
          btoa(
            this.openedContent.dirname +
              "/" +
              "songlist/" +
              (url.substring(0, 3) == "../" ? "" : this.openedSong.url + "/") +
              (url.substring(0, 1) == "/" ? url.substring(1) : url).replace(
                "../",
                ""
              )
          );
  }
  async loadLyric() {
    if (this.contentPath === "") {
      if (this.openedContent.stream_location !== "") {
        this.contentPath = this.openedContent.stream_location;
      } else {
        this.contentPath = this.openedContent.download_location;
      }
    }
    this.backsong = this.resolveurl(this.songs.data.backsong);
    this.vocal = this.resolveurl(this.songs.data.vocal);

    let aud = <HTMLVideoElement>document.getElementById("backsong");
    if (aud) {
      this.backsongEl = aud;
      aud.src = this.backsong;
    }
    let aud1 = <HTMLVideoElement>document.getElementById("vocal");
    if (aud1) {
      aud1.src = this.vocal;
      this.vocalEl = aud1;
    }

    this.textCurrentDuration = this.songs.data.duration;
    this.currentDuration = this.convertTime(this.songs.data.duration);

    this.animation = gsap.timeline({ paused: true });
    this.lyrics = Object.entries(this.songs.lyrics);
    this.lyrics.push([
      this.textCurrentDuration,
      {
        section: "Ending",
        singer: "",
        lyrics: {
          [this.textCurrentDuration]: "",
        },
      },
    ]);
    this.lyricCount = this.lyrics.length;
    this.lyrics.forEach((e, i) => {
      let delay = this.convertTime(e[0]);
      let wrapper = document.createElement("div");
      wrapper.id = "wrapper" + i;
      wrapper.style.cssText =
        "width:100%;height:100%;display:block;position:absolute;top:0px;left:0px";
      document.getElementById("content").appendChild(wrapper);

      let wrap = document.createElement("div");
      let section = document.createElement("div");
      let singer = document.createElement("div");
      let lyricwrap = document.createElement("div");
      let lyric = document.createElement("div");
      let nolyric = document.createElement("div");
      wrap.style.cssText =
        "display:flex;flex-direction:column;width:90%;max-width:1200px;height:100%;margin:auto;overflow:hidden";
      section.style.cssText =
        "display:block;min-height:15vh;line-height:15vh;font-size: clamp(1.6rem, 3vw, 2.4rem);font-weight:bold";
      singer.style.cssText =
        "display:block;min-height:8vh;line-height:8vh;font-size: clamp(1.2rem, 2vw, 1.6rem);";
      lyricwrap.style.cssText = "display:block;flex-grow:1;position:relative";
      lyric.style.cssText =
        "display:block;font-size: clamp(1.6rem, 2.4vw, 3rem);position:absolute;width:100%;height:100%;top:0px;left:0px;";
      nolyric.style.cssText =
        "display:block;font-size: clamp(1.6rem, 2.4vw, 3rem);flex-grow:1;color:grey;position:absolute;width:100%;height:100%;top:0px;left:0px;";
      // singer.style.cssText =
      //   "display:flex;min-height:80px;background:yellow;align-items:center;justify-content:flex-start";
      wrap.className = "wrap";
      // section.className = "section";
      // singer.className = "singer";
      lyric.className = "lyric";
      nolyric.className = "nolyric";
      lyric.id = "lyric" + i;
      section.innerHTML = e[1]["section"];
      singer.innerHTML = e[1]["singer"];
      nolyric.innerHTML = "LYRICS OFF";
      wrap.appendChild(section);
      wrap.appendChild(singer);
      lyricwrap.appendChild(lyric);
      lyricwrap.appendChild(nolyric);
      wrap.appendChild(lyricwrap);
      wrapper.appendChild(wrap);
      let textss = Object.entries(e[1].lyrics);
      textss.forEach((el, il) => {
        let texts = el;
        let text;
        let txt = texts[1];
        if (texts[1] == "<br>") {
          text = document.createElement("p");
          text.style.cssText = "margin-bottom:clamp(1.2rem, 2vw, 2.2rem)";
        } else {
          text = document.createElement("span");
          text.style.cssText = "display:inline-block";
          text.innerHTML = txt;
        }
        text.id = "text" + i + "t" + il;
        document.getElementById("lyric" + i).appendChild(text);
        setTimeout(() => {
          this.animation.to(
            "#text" + i + "t" + il,
            0.1,
            { color: "red", y: -5 },
            this.convertTime(el[0]) - 0.2
          );
          // if (this.lyrics.length - 1 > i) {
          //   this.animation.to(
          //     "#text" + i + "t" + il,
          //     0.5,
          //     { scale: 1.2 },
          //     this.convertTime(this.lyrics[i + 1][0])
          //   );
          // }
        }, 1000);
      });

      this.animation.from(
        "#wrapper" + i,
        0.3,
        {
          opacity: 0,
          onStart: () => {
            this.currentLyric = i;
          },
        },
        delay
      );
      this.animation.addLabel(i);
      if (this.lyrics.length - 1 > i) {
        this.animation.to(
          "#wrapper" + i,
          0.3,
          { opacity: 0 },
          this.convertTime(this.lyrics[i + 1][0])
        );
      }
    });
    let ct;
    ct = document.getElementById("content");
    if (this.songs.data.backgroundImage !== "") {
      ct.style.cssText =
        "background:url('" +
        this.resolveurl(this.songs.data.backgroundImage)
          .replace("file:\\", "file://")
          .replace(/\\/g, "/") +
        "');background-size:cover;background-position:center center;text-align:" +
        this.songs.data.alignment;
    } else {
      ct.style.cssText =
        "background:" +
        this.songs.data.backgroundColor +
        ";text-align:" +
        this.songs.data.alignment;
    }
    this.registerRange();
    this.showControl();

    if (!this.platform.is("electron")) {
      this.doPreLoad();
      // setTimeout(() => {
      //   this.hideLoading();
      // }, 1000);
    } else {
      setTimeout(() => {
        this.hideLoading();
      }, 1000);
    }
  }
  async doPreLoad() {
    let elements = [];
    let loaded = 0;
    await document.querySelectorAll("video").forEach((e) => {
      elements.push(e);
    });
    await document.querySelectorAll("audio").forEach((e) => {
      elements.push(e);
    });
    await document.querySelectorAll("img").forEach((e) => {
      elements.push(e);
    });
    elements.forEach((e) => {
      if (e.src.includes("https://www.dropbox.com")) {
        e.load();
        e.addEventListener("canplaythrough", () => {
          loaded++;
          this.loaded = Math.floor((loaded / elements.length) * 100);
          if (loaded == elements.length) this.hideLoading();
        });
      } else {
        let xhrReq = new XMLHttpRequest();
        xhrReq.open("GET", e.src, true);
        xhrReq.responseType = "blob";
        xhrReq.onload = () => {
          if (xhrReq.status === 200) {
            let vid = URL.createObjectURL(xhrReq.response);
            e.src = vid;
            loaded++;
            this.loaded = Math.floor((loaded / elements.length) * 100);
            if (loaded == elements.length) this.hideLoading();
          } else {
            loaded++;
            this.loaded = Math.floor((loaded / elements.length) * 100);
            if (loaded == elements.length) this.hideLoading();
          }
        };
        xhrReq.onerror = () => {
          console.log("err", arguments);
          loaded++;
          this.loaded = Math.floor((loaded / elements.length) * 100);
          if (loaded == elements.length) this.hideLoading();
        };
        xhrReq.send();
      }
    });
  }
  registerRange() {
    this.volume = 50;
    document.getElementById("volume").addEventListener("input", () => {
      this.changeVolume();
      this.extendshowControl();
    });
    document.getElementById("seeker").addEventListener("input", () => {
      this.seek();
      this.extendshowControl();
    });
    this.unMuteVocal();
    this.showLyric();
  }
  hideLoading() {
    document.getElementById("page").style.display = "block";
    document.getElementById("loading").style.display = "none";
    gsap.fromTo(
      "#wrapper" + this.currentLyric,
      { opacity: 0 },
      { opacity: 1, display: "flex", duration: 0.3 }
    );
  }
  convertTime(time) {
    let tm = time.split(":");
    return parseInt(tm[0]) * 60 + parseFloat(tm[1]);
  }
  convertDuration(seconds) {
    return [
      // parseInt("" + seconds / 60 / 60),
      parseInt("" + ((seconds / 60) % 60)),
      parseInt("" + (seconds % 60)),
    ]
      .join(":")
      .replace(/\b(\d)\b/g, "0$1");
  }
  extendshowControl() {
    if (this.isPlaying == true) {
      clearTimeout(this.controlTimeout);
      this.controlTimeout = setTimeout(() => {
        this.control.style.display = "none";
        this.visibleControl = false;
      }, 3000);
    }
  }
  showControl() {
    if (this.visibleControl == false) {
      this.control.style.display = "flex";
      this.visibleControl = true;
      if (this.isPlaying == true) {
        this.controlTimeout = setTimeout(() => {
          this.control.style.display = "none";
          this.visibleControl = false;
        }, 3000);
      }
    } else {
      this.control.style.display = "none";
      this.visibleControl = false;
      clearTimeout(this.controlTimeout);
    }
  }
  showControlMove() {
    if (this.visibleControl == false) {
      this.control.style.display = "flex";
      this.visibleControl = true;
      if (this.isPlaying == true) {
        this.controlTimeout = setTimeout(() => {
          this.control.style.display = "none";
          this.visibleControl = false;
        }, 3000);
      }
    }
  }
  showVideoModal() {
    // this.pausePlay();
    this.videoModalSrc = this.resolveurl(this.songs.data.video);
    this.pauseSong();
    this.control.style.display = "none";
    this.visibleControl = false;
    this.videoModal.style.display = "flex";
    this.videoModalVideo.currentTime = 0;
    this.videoModalVideo.play();
  }
  hideVideoModal() {
    this.videoModalVideo.pause();
    this.videoModal.style.display = "none";
  }
  showPlaylistModal() {
    this.control.style.display = "none";
    this.visibleControl = false;
    this.playlistModal.style.display = "block";
  }
  hidePlaylistModal() {
    this.playlistModal.style.display = "none";
  }
  openFullscreen() {
    let elem = this.page;
    if (this.isFullScreen == false) {
      if (elem.requestFullscreen) {
        elem.requestFullscreen();
      } else if (elem.webkitRequestFullscreen) {
        /* Safari */
        elem.webkitRequestFullscreen();
      } else if (elem.msRequestFullscreen) {
        /* IE11 */
        elem.msRequestFullscreen();
      }
      this.isFullScreen = true;
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen();
      }
      this.isFullScreen = false;
    }
    this.extendshowControl();
  }
  changeVolume() {
    this.backsongEl.volume = this.volume / 100;
    this.vocalEl.volume = this.volume / 100;
    this.extendshowControl();
  }
  figureOutFile(file: string) {
    if (this.platform.is("ios")) {
      const baseUrl = location.href.replace("/index.html", "");
      return baseUrl + `${file}`;
    }
    if (this.platform.is("android")) {
      return `file:///android_asset/www${file}`;
    }
  }
  openPDF() {
    // let pdf = this.contentPath + this.songs.data.pdf;
    let pdf = this.resolveurl(this.songs.data.pdf);
    if (this.platform.is("desktop") || this.platform.is("mobileweb")) {
      var iframe =
        '<html><head><style>body, html {width: 100%; height: 100%; margin: 0; padding: 0}</style></head><body><iframe src="' +
        pdf +
        '" style="height:calc(100% - 0px);width:calc(100% - 0px);border:none"></iframe></html></body>';
      var win = window.open(
        "",
        "",
        "width=600,height=480,toolbar=no,menubar=no,resizable=yes"
      );
      win.document.write(iframe);
    } else {
      // const options: DocumentViewerOptions = {
      //   title: "My PDF",
      // };
      // this.document.viewDocument(
      //   this.figureOutFile("/assets/l1plan.pdf"),
      //   "application/pdf",
      //   options
      // );
    }
  }
  seek() {
    clearInterval(this.seekerTimer);
    this.backsongEl.currentTime = (this.seeker / 1000) * this.currentDuration;
    this.vocalEl.currentTime = (this.seeker / 1000) * this.vocalEl.duration;
    this.updateSeeker();
  }
  updateSeeker() {
    this.seekerTimer = setInterval(() => {
      this.seeker = (this.backsongEl.currentTime / this.currentDuration) * 1000;
      this.animation.progress(
        this.backsongEl.currentTime / this.currentDuration
      );
      this.progress = this.convertDuration(this.backsongEl.currentTime);
      if (this.seeker >= 1000) clearInterval(this.seekerTimer);
    }, 100);
    this.extendshowControl();
  }
  goToLyric(num) {
    this.currentLyric = num;
    this.changeLyric();
  }
  pauseSong() {
    this.backsongEl.pause();
    this.vocalEl.pause();
    clearInterval(this.seekerTimer);
    this.isPlaying = false;
    this.mintl1.restart();
    this.extendshowControl();
  }
  playSong() {
    this.backsongEl.play();
    this.vocalEl.play();
    this.updateSeeker();
    this.isPlaying = true;
    this.mintl2.restart();
    this.extendshowControl();
  }
  stopSong() {
    this.backsongEl.pause();
    this.backsongEl.currentTime = 0;
    this.animation.progress(0);
    this.seeker = 0;
    this.progress = "00:00";
    this.currentLyric = 0;
    clearInterval(this.seekerTimer);
    this.isPlaying = false;
    this.vocalEl.pause();
    this.vocalEl.currentTime = 0;
    this.extendshowControl();
  }
  muteVocal() {
    this.backsongEl.muted = false;
    this.vocalEl.muted = true;
    this.mutedVocal = true;
    this.extendshowControl();
  }
  unMuteVocal() {
    this.backsongEl.muted = true;
    this.vocalEl.muted = false;
    this.mutedVocal = false;
    this.extendshowControl();
  }
  hideLyric() {
    gsap.to(".lyric", 0.1, { opacity: 0 });
    gsap.to(".nolyric", 0.1, { opacity: 1 });
    this.lyricHidden = true;
    this.extendshowControl();
  }
  showLyric() {
    gsap.to(".lyric", 0.1, { opacity: 1 });
    gsap.to(".nolyric", 0.1, { opacity: 0 });
    this.lyricHidden = false;
    this.extendshowControl();
  }
  nextLyric() {
    this.currentLyric++;
    this.changeLyric();
    this.extendshowControl();
  }
  prevLyric() {
    this.currentLyric--;
    this.changeLyric();
    this.extendshowControl();
  }
  rewindLyric() {
    this.changeLyric();
    this.extendshowControl();
  }
  changeLyric() {
    let lyric = Object.entries(this.songs.lyrics);

    this.seeker =
      (this.convertTime(lyric[this.currentLyric][0]) / this.currentDuration) *
      1000;
    this.seek();
  }
  registerKey(ke) {
    if (ke.which == 32) {
      if (this.isPlaying !== true) this.playSong();
      else this.pauseSong();
    }
    if (ke.keyCode == 37) {
      this.prevLyric();
    }
    if (ke.keyCode == 39) {
      this.nextLyric();
    }
    if (ke.keyCode == 70) {
      this.openFullscreen();
    }
    if (ke.keyCode == 86) {
      if (this.videoModal.style.display == "none") {
        this.showVideoModal();
      } else {
        this.hideVideoModal();
      }
    }
    if (ke.keyCode == 80) {
      this.openPDF();
    }
    if (ke.keyCode == 76) {
      if (this.playlistModal.style.display == "none") {
        this.showPlaylistModal();
      } else {
        this.hidePlaylistModal();
      }
    }
  }
  onTap(e) {
    const now = Date.now();
    if (Math.abs(now - this.lastOnStart) <= this.DOUBLE_CLICK_THRESHOLD) {
      if (this.isPlaying == false) {
        this.playSong();
      } else {
        this.pauseSong();
      }
      this.lastOnStart = 0;
    } else {
      this.lastOnStart = now;
    }
    setTimeout(() => {
      if (now - this.lastOnStart == 0) {
        this.showControl();
      }
    }, this.DOUBLE_CLICK_THRESHOLD);
  }
  onSwipe(e) {
    this.preventSingleClick = true;
    clearTimeout(this.timer);
    if (e.type == "swipe") {
      if (e.deltaX >= 100) {
        this.prevLyric();
      }
      if (e.deltaX <= -100) {
        this.nextLyric();
      }
    }
  }
  private xf = this.registerKey.bind(this);
  ngOnDestroy(): void {
    this.stopSong();
    this.unregisterAll();
    this.backsongEl.src = "";
    this.vocalEl.src = "";
  }
  registerAll() {
    document.addEventListener("keydown", this.xf, false);
  }
  unregisterAll() {
    document.removeEventListener("keydown", this.xf, false);
  }
}
